import {CommonService} from "../services/commonService";
import {Helmet} from "react-helmet";

const HomePage = () => {
    const commonApi = new CommonService()
    return <div>
        <Helmet>
            <meta name="keywords" content="Реакт, оп, им, ап"/>
        </Helmet>
        <button onClick={async () => {
            await commonApi.testGet().then((res) => console.log(res.data)).catch((e) => console.log(e))
        }}>
            test get
        </button>
        <button onClick={async () => {
            await commonApi.testPost().then((res) => console.log(res.data))
        }}>
            test post
        </button>
    </div>
}

export default HomePage